.Button {
  cursor: pointer;
  box-shadow: 0 1px 3px 0 rgb(0 0 0 / 10%), 0 1px 2px 0 rgb(0 0 0 / 6%);
  font-size: .75rem;
  text-transform: uppercase;
  padding-left: 2.5rem;
  padding-right: 2.5rem;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  margin-left: auto;
  margin-right: auto;
  display: block;
  border-radius: 0.25rem;
  background-color: #434190;
  color: #fff;
}

.Button:hover {
  background-color: #4c51bf;
}
